___CSS_LOADER_EXPORT___.push([module.id, `body {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 33px;
  min-height: 100vh;
  background-color: #241f31;
}

h1,
h2,
h3,
label {
  margin: 0;
  margin-bottom: 10px;
  color: white;
  text-align: center;
}
div#container {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  justify-content: center;
  align-items: center;
}

div.ships > div.container {
  display: flex;
  gap: 5px;
}
div.ship > div.block {
  width: 2vw;
  height: 2vw;
  background-color: blue;
  border: 1px black solid;
}

div.boards {
  display: grid;
  grid-template: 1fr / repeat(2, 1fr);
  gap: 25px;
  min-width: fit-content;
  width: 60vw;
  height: 30vw;
}
div.player {
  display: flex;
  gap: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
div.board {
  display: grid;
  grid-template: repeat(10, 1fr) / repeat(10, 1fr);
  grid-auto-flow: column;
  gap: 1px;
  width: calc(100% - 36px);
  height: 100%;
  border: 1px black solid;
  background-color: black;
}
div.position {
  background-color: white;
}

div.buttons {
  display: flex;
  gap: 25px;
}
button {
  background-color: blue;
  color: white;
  font-size: 1.15rem;
  font-weight: bold;
  border-radius: 5px;
  padding: 10px;
}
